<script lang="ts">
	import Result from '../result/result.svelte'
	import { onMount, afterUpdate } from 'svelte';
	import { Lotto845 } from '../lotto.ts';

	export let name;
	export let rounds: number[];

	let round = 0;
	let selected_round = [];
	let cnt: number = 1;
	let parchase_cnt = 0;;
	let result_list = null;

	function get_random_nums(){
		const result = Array.from({length: 45}, (_, i) => i+1).sort( _ => Math.random() > .5 ? 1 : -1).slice(0, 6);
		result_list.add(result);
		result_list.get_statics();
		result_list = result_list;
	};

	afterUpdate(() => {
		if (rounds.length && round === 0) {
			selected_round = rounds[rounds.length - 1];
			round = selected_round[0];
		}
		if (result_list === null && selected_round.length) {
			result_list = new Lotto845([], selected_round)
		}
	})

	$: money = parchase_cnt * 1000;

	function set_round(r) {
		selected_round = rounds[r - 1];
		if (selected_round) {
			round = r;
			result_list.set_win_num(selected_round);
		} else {
			alert("존재하지 않는 회차입니다.");
		}
	}

	function parchase(num: number) {
		if (round) {
			let i = 0;
			parchase_cnt += num;
			while (i < num) {
				get_random_nums()
				i++;
			}
		} else {
			alert('회차를 선택해주세요.');
		}
	}

</script>

<style lang="scss">
	@import '../main.scss';
	main {
		max-height: 100vh;
		padding: 16px;
		overflow-y: auto;
	}
</style>

<main class="grid-y">

	<div class="grid-x grid-padding-x">
		<div class="large-6 cell">
			<div class="input-group">
				<span class="input-group-label">갯수</span>
				<input class="input-group-field" id="cnt" type="number" bind:value={cnt} on:enter={() => parchase(cnt)}>
				<div class="input-group-button">
					<button class="button primary" on:click={() => parchase(cnt)}>
						총 {money}원 구매
					</button>
				</div>
			</div>
		</div>
		<div class="large-6 cell">
			<div class="input-group">
				<span class="input-group-label">
					{selected_round[0]  ? '회차' : '회차를 입력하세요'}
				</span>
				<input type="number" class="input-group-field" bind:value={round} on:change={({target: {value}}) => set_round(value)}>
				<div class="input-group-button">
					<button class="button primary" on:click={() => set_round(round)}>
						확인
					</button>
				</div>
			</div>
		</div>
	</div>

	{#if result_list}
		<div class="grid-x grid-padding-x">
			<div class="large-12 cell">
				<div class="table">
				<table>
					<thead>
						<tr>
							<th>1등</th>
							<th>2등</th>
							<th>3등</th>
							<th>4등</th>
							<th>5등</th>
							<th>꽝</th>
						</tr>
					</thead>
					<tbody>
						<tr>
							<td>{result_list?.statics[1]}</td>
							<td>{result_list?.statics[2]}</td>
							<td>{result_list?.statics[3]}</td>
							<td>{result_list?.statics[4]}</td>
							<td>{result_list?.statics[5]}</td>
							<td>{result_list?.statics[0]}</td>
						</tr>
					</tbody>
				</table>
			</div>
			</div>

		</div>
	{/if}
	<!-- 손해금액 -->

	{#if result_list}
		{#each result_list.list as item }
			<div class="ball_container">
				{#each item as num, i}
					{#if i == item.length - 1}
						<Result nums={item} round_info={selected_round} rank={num} />
					{:else}
						<div class={`ball color_${Math.floor(num/10)}n`}>{num}</div>
					{/if}
				{/each}
			</div>
		{/each}
	{:else}
		존재하지 않는 회차입니다
	{/if}
</main>