<script lang="ts">
  import { onMount } from 'svelte';
  import LottoMachine from "./threejs/lotto_machine";

  let el: HTMLCanvasElement;
  let is_open = false;

  onMount(() => {
    new LottoMachine(el);
  });

  const toggle = () => {
    is_open = !is_open;
  }

</script>

<style lang="scss">
  .close {
    height: 0;
    overflow: hidden;
  }
</style>

<main>
  <button
    type="button" class="success button"
    on:click={toggle}>Toggle Canvas</button>

  <div class={`canvas_container ${is_open ? 'open' : 'close'}`}>
    <canvas bind:this={el}></canvas>
  </div>
</main>