import { GUI } from 'dat.gui';
import * as THREE from 'three';
import { createControl } from './etc';
import { MinMaxGUIHelper, getCamera } from './camera';
import { getLight } from './light';
import { make_floor, make_ball_meshs } from './mesh';
import * as CANNON from 'cannon-es';
class LottoMachine {
    constructor(el) {
        this.sphereShadowBases = [];
        this.createScene = (canvas) => {
            this.canvasEl = canvas;
            this.scene = new THREE.Scene();
            this.camera = getCamera();
            this.light = getLight();
            this.camera.position.set(20, 20, 10);
            this.control = createControl(this.camera, this.canvasEl);
            this.renderer = new THREE.WebGLRenderer({
                antialias: true,
                canvas: this.canvasEl,
                logarithmicDepthBuffer: true,
            });
            this.scene.background = new THREE.Color('white');
            const floor = make_floor();
            this.scene.add(this.camera);
            this.scene.add(this.light);
            this.scene.add(floor);
            this.resize();
            this.render();
            window.addEventListener('resize', this.resize);
        };
        // 사용안함
        this.add_shadow_ani = (time) => {
            time *= 0.001; // 초 단위로 변환
            this.sphereShadowBases.forEach((sphereShadowBase, ndx) => {
                const { base, sphereMesh, shadowMesh, y } = sphereShadowBase;
                // u는 구체의 반복문을 실행하면서 인덱스에 따라 0 이상, 1 이하로 지정됩니다
                const u = ndx / this.sphereShadowBases.length;
                /**
                 * 컨테이너의 위치를 계산합니다. 구체와 그림자가
                 * 컨테이너에 종속적이므로 위치가 같이 변합니다
                 */
                const speed = time * .2;
                const angle = speed + u * Math.PI * 2 * (ndx % 1 ? 1 : -1);
                const radius = Math.sin(speed - ndx) * 10;
                base.position.set(Math.cos(angle) * radius, 0, Math.sin(angle) * radius);
                // yOff 값은 0 이상 1 이하입니다
                const yOff = Math.abs(Math.sin(time * 2 + ndx));
                // 구체를 위아래로 튕김
                sphereMesh.position.y = y + THREE.MathUtils.lerp(-2, 2, yOff);
                if (sphereMesh.position.y < 0)
                    console.log(sphereMesh.position.y, { yOff, y });
                // 구체가 위로 올라갈수록 그림자가 옅어짐
                shadowMesh.material.opacity = THREE.MathUtils.lerp(1, .25, yOff);
            });
        };
        this.render = () => {
            requestAnimationFrame(this.render);
            // requestAnimationFrame(this.add_shadow_ani);
            this.updatePhysics();
            this.renderer.render(this.scene, this.camera);
        };
        this.resize = () => {
            const width = window.innerWidth / 3 * 2;
            const height = window.innerHeight / 3 * 2;
            this.renderer.setSize(width, height);
            this.camera.aspect = width / height;
            this.camera.updateProjectionMatrix(); // resize시 계단현상을 없엠
        };
        this.updateCamera = () => {
            this.camera.updateProjectionMatrix();
        };
        this.makeGUIhelper = () => {
            const gui = new GUI({ autoPlace: false });
            gui.add(this.camera, 'fov', 1, 180).onChange(this.updateCamera);
            const minMaxGUIHelper = new MinMaxGUIHelper(this.camera, 'near', 'far', 0.1);
            gui.add(minMaxGUIHelper, 'min', 0.1, 50, 0.1).name('near').onChange(this.updateCamera);
            gui.add(minMaxGUIHelper, 'max', 0.1, 50, 0.1).name('far').onChange(this.updateCamera);
            this.gui = gui;
            const container = document.querySelector(".canvas_container");
            if (container) {
                container.appendChild(this.gui.domElement);
            }
        };
        if (el && el.tagName == "CANVAS") {
            this.createScene(el);
            this.world = new CANNON.World();
            this.world.broadphase = new CANNON.NaiveBroadphase();
            //공 렌더링
            Array.from({ length: 45 }).map((_, i) => {
                const item = make_ball_meshs(i);
                this.sphereShadowBases.push(item);
                this.scene.add(item.sphereMesh);
                this.world.addBody(item.sphereMesh.body);
            });
            this.makeGUIhelper();
            this.world.gravity.set(0, -50, 0);
            // 물리현상 재현을 위한 cannonjs
            const plane = new CANNON.Plane();
            const groundBody = new CANNON.Body({ mass: 0 });
            groundBody.addShape(plane);
            groundBody.quaternion.setFromAxisAngle(new CANNON.Vec3(1, 0, 0), -Math.PI / 2);
            this.world.addBody(groundBody);
            this.mouse = new THREE.Vector2();
            this.raycaster = new THREE.Raycaster();
            // Bind events
            document.addEventListener("click", (e) => { this.onClick(e); });
            window.addEventListener("mousemove", e => { this.onMouseMove(e); });
        }
    }
    updatePhysics() {
        if (this.world) {
            this.world.step(1 / 60);
            this.sphereShadowBases.map(item => {
                const { body } = item.sphereMesh;
                item.sphereMesh.position.x = body.position.x;
                item.sphereMesh.position.y = body.position.y;
                item.sphereMesh.position.z = body.position.z;
                item.sphereMesh.quaternion.x = body.quaternion.x;
                item.sphereMesh.quaternion.y = body.quaternion.y;
                item.sphereMesh.quaternion.z = body.quaternion.z;
                item.sphereMesh.quaternion.w = body.quaternion.w;
            });
        }
    }
    onMouseMove(event) {
        // We set the normalized coordinate of the mouse
        this.mouse.x = (event.clientX / window.innerWidth) * 2 - 1;
        this.mouse.y = -(event.clientY / window.innerHeight) * 2 + 1;
    }
    onClick(e) {
        if (e.target.tagName !== 'CANVAS')
            return false;
        // // update the picking ray with the camera and mouse position
        // this.raycaster.setFromCamera(this.mouse, this.camera);
        // calculate objects intersecting the picking ray
        // It will return an array with intersecting objects
        const intersects = this.raycaster.intersectObjects(this.scene.children, true);
        if (intersects.length > 0) {
            const obj = intersects[0];
            const { object, face } = obj;
            // if (!object.isObject3D) return;
            const impulse = new THREE.Vector3()
                .copy(face.normal)
                .negate()
                .multiplyScalar(30);
            this.sphereShadowBases.map(item => {
                const { body } = item.sphereMesh;
                body.applyLocalImpulse(impulse, new CANNON.Vec3());
            });
        }
    }
}
export default LottoMachine;
