<script lang="ts">
	import { onMount } from 'svelte';
	import {parse_csv} from '../data_parser'
	import GistLoader from '../../../repository/gist/dataLoader'

	export let rounds: any[];

	let data_head: string[] = []
	let data_body: number[][] = []

	async function fetchData() {
		const dataLoader = new GistLoader('af700e6457ef5f5a4e6543c47c7ff76d')
		const data = await dataLoader.fetch();
		const {content} = data.files["kor_lotto_history.csv"];

		return parse_csv(content);
	}

	onMount(async () => {
		const [header, body] = await fetchData();
		data_head = header;
		data_body = body.map((item: string[]) => {
			return item.map((n, i: number) => {
				if (i != 1) {
					return parseInt(n)
				} else {
					return n;
				}
			});
		})
		rounds = data_body;
	})
</script>

<style lang="scss">
	/* @import '../main.scss'; */
	.table-scroll {
		overflow: auto;
		max-height: 100px;
	}
</style>

<div class="table-scroll">
	<table>
		<thead>
			<tr>
				{#each data_head as title}
				<td>
					{title}
				</td>
				{/each}
			</tr>
		</thead>
		<tbody>
			{#each data_body as row}
				<tr>
					{#each row as num}
					<td>
						<div class={`ball color_${Math.floor(num/10)}n`}>{num}</div>
					</td>
					{/each}
				</tr>
			{/each}
		</tbody>
	</table>
</div>