import * as THREE from "three";
import { Mesh, MeshPhysicalMaterial, SphereGeometry } from 'three'; // gui
import * as CANNON from 'cannon-es';
const sphereRadius = 1;
const widthSegments = 32;
const heightSegments = 16;
export const geometry = new SphereGeometry(sphereRadius, widthSegments, heightSegments);
export const BALL_COLORS = ['#fbc400', '#69c8f2', '#ff7272', '#aaa', '#b0d840'];
const circle_floor = () => {
    const planeGeo = new THREE.SphereGeometry(sphereRadius * 50, widthSegments * 50, heightSegments * 50);
    const planeMat = new THREE.MeshBasicMaterial({
        side: THREE.DoubleSide
    });
    planeMat.color.setRGB(1.5, 1.5, 1.5);
    const mesh = new THREE.Mesh(planeGeo, planeMat);
    mesh.rotation.x = Math.PI * -0.5;
    return mesh;
};
export const make_floor = () => {
    if (false) {
        return circle_floor();
    }
    else {
        const planeSize = 40;
        const loader = new THREE.TextureLoader();
        const texture = loader.load('https://threejsfundamentals.org/threejs/resources/images/checker.png');
        texture.wrapS = THREE.RepeatWrapping;
        texture.wrapT = THREE.RepeatWrapping;
        texture.magFilter = THREE.NearestFilter;
        const repeats = planeSize / 2;
        texture.repeat.set(repeats, repeats);
        const planeGeo = new THREE.PlaneGeometry(planeSize, planeSize);
        const planeMat = new THREE.MeshBasicMaterial({
            map: texture,
            side: THREE.DoubleSide
        });
        planeMat.color.setRGB(1.5, 1.5, 1.5);
        const mesh = new THREE.Mesh(planeGeo, planeMat);
        mesh.rotation.x = Math.PI * -0.5;
        return mesh;
    }
};
const loader = new THREE.TextureLoader();
