export const make_ball_meshs = (i) => {
    const base = new THREE.Object3D();
    const div = Math.floor(i / 10);
    const mod = i % 10;
    const ball_color = BALL_COLORS[div];
    const ball_material = new MeshPhysicalMaterial({ color: ball_color });
    geometry.computeBoundingBox();
    geometry.computeBoundingSphere();
    const sphereMesh = new Mesh(geometry, ball_material);
    const ball_x = div * 2 - window.innerWidth / 200;
    const ball_z = mod * 2 - window.innerHeight / 200;
    sphereMesh.position.set(ball_x, sphereRadius + 2, ball_z);
    // const shadowMat = new THREE.MeshBasicMaterial({
    // 	map: shadowTexture,
    // 	transparent: true, // 땅이 보이도록
    // 	depthWrite: false // 그림자를 따로 정렬하지 않도록
    // });
    // const shadowMesh = new THREE.Mesh(shadowGeo, shadowMat);
    // shadowMesh.position.y = 0.001; // 그림자를 땅에서 살짝 위에 배치
    // shadowMesh.position.x = ball_x;
    // shadowMesh.position.z = ball_z;
    // shadowMesh.rotation.x = Math.PI * -0.5;
    // const shadowSize = sphereRadius * 4;
    // shadowMesh.scale.set(shadowSize, shadowSize, shadowSize);
    // base.add(shadowMesh);
    base.add(sphereMesh);
    const c_ball = new CANNON.Sphere(sphereRadius);
    // console.log(c_ball);
    sphereMesh.size = sphereMesh.geometry.boundingBox.getSize(new THREE.Vector3());
    // Create the shape of our letter
    // Note that we need to scale down our geometry because of Box'Cannon class setup
    const box = new CANNON.Box(new CANNON.Vec3().copy(sphereMesh.size).scale(0.5));
    // Attach the body directly to the mesh
    const c_material = new CANNON.Material();
    sphereMesh.body = new CANNON.Body({
        mass: 0.5,
        position: new CANNON.Vec3(ball_x, sphereRadius + 2, ball_z),
        material: c_material
    });
    const { center } = sphereMesh.geometry.boundingSphere;
    sphereMesh.body.addShape(box, new CANNON.Vec3(center.x, center.y, center.z));
    return {
        c_ball,
        base,
        sphereMesh,
        // shadowMesh,
        y: sphereMesh.position.y
    };
};
export const make_container = () => {
    const pts = [
        new THREE.Vector2(3 + 2, 0),
        new THREE.Vector2(3 + 2 - 1, 0),
        new THREE.Vector2(3 + 2 - 1, 0),
        new THREE.Vector2(3 + 2, 0),
        new THREE.Vector2(3 + 2, 0) //back to top left - close square path
    ];
    const geometry = new THREE.LatheGeometry(pts, 100);
    const material = new THREE.MeshLambertMaterial({ color: '#ff0' });
    const ring = new THREE.Mesh(geometry, material);
    return ring;
};
