export function parse_csv(content) {
    const table = content.split('\n').reduce((acc, curr, index) => {
        const row = curr.split(',');
        if (index === 0) {
            acc[0] = row;
        }
        else {
            if (row.length > 1) {
                acc[1].push(row);
            }
        }
        return acc;
    }, [[], []]);
    return table;
}
