import { PerspectiveCamera } from 'three'; // gui
const camera_config = {
    fov: 90,
    aspect: (window.innerWidth / window.innerHeight) / 3 * 2,
    near: 1,
    far: 50
};
export const getCamera = () => new PerspectiveCamera(camera_config.fov, camera_config.aspect, camera_config.near, camera_config.far);
// 카메라 헬퍼
export class MinMaxGUIHelper {
    constructor(obj, minProp, maxProp, minDif) {
        this.obj = obj;
        this.minProp = minProp;
        this.maxProp = maxProp;
        this.minDif = minDif;
    }
    get min() {
        return this.obj[this.minProp];
    }
    set min(v) {
        this.obj[this.minProp] = v;
        this.obj[this.maxProp] = Math.max(this.obj[this.maxProp], v + this.minDif);
    }
    get max() {
        return this.obj[this.maxProp];
    }
    set max(v) {
        this.obj[this.maxProp] = v;
        this.min = this.min; // min setter로 작동
    }
}
