class GistLoader {
    constructor(gistID) {
        this.URL = 'https://api.github.com/gists/';
        this.gistID = '';
        this.gistID = gistID;
    }
    async fetch() {
        const res = await fetch(this.URL + this.gistID);
        const json = await res.json();
        return json;
    }
}
export default GistLoader;
